// --- Draggable Header Component ---
interface DraggableHeaderProps {
  column: ColumnConfig; // Use 'column' prop
}

const DraggableHeader: React.FC<DraggableHeaderProps> = ({ column }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: column.id }); // Use column.id

  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1, // Dim when dragging
    touchAction: 'none', // Prevent scrolling on touch devices while dragging
  };

  // Determine alignment based on column ID
  const isNumeric = ['quantity', 'pricePerItem'].includes(column.id);

  return (
    // Use MUI TableCell as the sortable header element
    <TableCell 
      ref={setNodeRef} 
      component="th" // Specify it's a header cell
      scope="col" 
      style={style} 
      {...attributes} 
      {...listeners}
      align={isNumeric ? 'right' : 'left'} // Set alignment
      sx={{ 
        fontWeight: 'bold', 
        cursor: 'grab', 
        whiteSpace: 'nowrap',
        bgcolor: isDragging ? '#e0e0e0' : 'background.paper', // Ensure header stays opaque, highlight on drag
        // Apply specific widths/minWidths based on column ID
        ...(column.id === 'isFlagged' && { width: 50, p: '6px 8px' }),
        ...(column.id === 'seller' && { minWidth: 140 }),
        ...(column.id === 'deliveryName' && { minWidth: 160 }),
        ...(column.id === 'quantity' && { width: 80 }), // Already aligned right
        ...(column.id === 'destination' && { minWidth: 110 }),
        ...(column.id === 'pricePerItem' && { width: 100 }), // Already aligned right
        ...(column.id === 'orderNumber' && { width: 110 }),
        ...(column.id === 'purchaseStatus' && { width: 110 }),
        ...(column.id === 'orderDate' && { width: 110 }),
        ...(column.id === 'asinSku' && { width: 120 }),
        ...(column.id === 'productName' && { minWidth: 160 }),
        ...(column.id === 'actions' && { width: 60, p: '6px 8px' }),
        ...(column.id === 'currentStatus' && { width: 100 }),
      }}
    >
      {column.label} {/* Use column.label */}
    </TableCell>
  );
};

